/* Purchases Module - Notion Style */

.purchases-container {
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: white;
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
    color: #37352f;
}

.purchases-header-wrapper {
    max-width: 900px;
    width: 100%;
    margin: 0 auto;
    padding: 48px 32px 24px 32px;
}

/* Breadcrumbs */
.purchases-breadcrumbs {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 14px;
    color: #9b9a97;
    margin-bottom: 24px;
    font-weight: 500;
}

/* Title */
.purchases-title-row {
    display: flex;
    align-items: center;
    gap: 16px;
    margin-bottom: 32px;
}

.purchases-emoji {
    font-size: 40px;
    line-height: 1;
}

.purchases-title {
    font-size: 40px;
    font-weight: 700;
    color: #37352f;
    letter-spacing: -0.02em;
    margin: 0;
}

/* Tabs */
.purchases-tabs {
    display: flex;
    align-items: center;
    gap: 32px;
    border-bottom: 1px solid #e9e9e8;
}

.purchases-tab {
    background: none;
    border: none;
    padding-bottom: 12px;
    font-size: 14px;
    font-weight: 500;
    color: #9b9a97;
    cursor: pointer;
    position: relative;
    transition: color 0.2s ease;
}

.purchases-tab:hover {
    color: #37352f;
}

.purchases-tab.active {
    color: #37352f;
}

.purchases-tab.active::after {
    content: '';
    position: absolute;
    bottom: -1px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #37352f;
}

/* Content Area */
.purchases-content {
    flex: 1;
    overflow-y: auto;
}

.purchases-inner-content {
    max-width: 900px;
    width: 100%;
    margin: 0 auto;
    padding: 24px 32px;
}

/* Callouts */
.notion-callout {
    display: flex;
    gap: 16px;
    background-color: #fbf3db;
    padding: 16px 16px;
    border-radius: 4px;
    margin-bottom: 32px;
    color: #37352f;
}

.notion-callout-icon {
    font-size: 24px;
    line-height: 1.2;
}

.notion-callout-content h2 {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 4px;
}

.notion-callout-content p {
    font-size: 14px;
    line-height: 1.5;
    color: #37352f;
    opacity: 0.9;
}

/* Tables */
.notion-table {
    width: 100%;
    border-collapse: collapse;
    font-size: 14px;
}

.notion-table th {
    text-align: left;
    font-weight: 400;
    color: #9b9a97;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    padding: 8px 12px;
    border-bottom: 1px solid #e9e9e8;
}

.notion-table td {
    padding: 8px 12px;
    border-bottom: 1px solid #f2f2f1;
    color: #37352f;
    vertical-align: middle;
}

.notion-table tr:hover td {
    background-color: #f7f7f5;
}

.notion-table-row {
    cursor: pointer;
}

/* Badges */
.notion-badge {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    padding: 2px 8px;
    border-radius: 4px;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.5;
}

.badge-gray {
    background-color: #f1f1ef;
    color: #4b4b4b;
}

.badge-orange {
    background-color: #faebd7;
    color: #d9730d;
}

.badge-red {
    background-color: #ffe2dd;
    color: #d44c47;
}

.badge-green {
    background-color: #dbeddb;
    color: #448361;
}

.badge-blue {
    background-color: #d3e5ef;
    color: #183347;
}

.badge-purple {
    background-color: #e8deee;
    color: #9065b0;
}

/* Buttons */
.notion-btn {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    padding: 6px 12px;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.1s;
    border: 1px solid rgba(55, 53, 47, 0.16);
    background: white;
    color: #37352f;
}

.notion-btn:hover {
    background: #f0f0f0;
}

.notion-btn-primary {
    background: #2383e2;
    color: white;
    border: none;
}

.notion-btn-primary:hover {
    background: #1d6fbf;
}

.notion-btn-ghost {
    background: transparent;
    border: none;
    padding: 6px;
    color: #9b9a97;
}

.notion-btn-ghost:hover {
    background: #f1f1ef;
    color: #37352f;
}

.notion-btn-icon {
    padding: 6px;
    border: none;
    color: #9b9a97;
}

/* Search */
.notion-search-wrapper {
    position: relative;
    width: 280px;
}

.notion-search-input {
    width: 100%;
    background: none;
    border: none;
    border-bottom: 1px solid #e9e9e8;
    padding: 8px 8px 8px 28px;
    font-size: 14px;
    outline: none;
    color: #37352f;
}

.notion-search-input:focus {
    border-bottom-color: #37352f;
}

/* Modals */
.notion-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(15, 15, 15, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    backdrop-filter: blur(2px);
}

.notion-modal {
    background: white;
    width: 100%;
    max-width: 600px;
    border-radius: 8px;
    box-shadow: 0 4px 24px rgba(0, 0, 0, 0.12);
    display: flex;
    flex-direction: column;
    max-height: 90vh;
}

.notion-modal-header {
    padding: 16px 24px;
    border-bottom: 1px solid #e9e9e8;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.notion-modal-header h3 {
    font-size: 16px;
    font-weight: 600;
    color: #37352f;
    margin: 0;
}

.notion-modal-body {
    padding: 24px;
    flex: 1;
    overflow-y: auto;
}

.notion-modal-footer {
    padding: 16px 24px;
    border-top: 1px solid #e9e9e8;
    display: flex;
    justify-content: flex-end;
    gap: 12px;
    background-color: #fbfbfa;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

/* Forms */
.notion-form-group {
    margin-bottom: 16px;
}

.notion-label {
    display: block;
    margin-bottom: 6px;
    font-size: 12px;
    font-weight: 500;
    color: #9b9a97;
    text-transform: uppercase;
}

.notion-input {
    width: 100%;
    padding: 8px 12px;
    font-size: 14px;
    border: 1px solid #e9e9e8;
    border-radius: 4px;
    color: #37352f;
    outline: none;
    transition: border-color 0.15s;
}

.notion-input:focus {
    border-color: #2383e2;
    box-shadow: 0 0 0 2px rgba(35, 131, 226, 0.1);
}

.notion-textarea {
    width: 100%;
    padding: 8px 12px;
    font-size: 14px;
    border: 1px solid #e9e9e8;
    border-radius: 4px;
    color: #37352f;
    outline: none;
    min-height: 80px;
    resize: vertical;
}

.notion-grid-2 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 16px;
}

.notion-col-span-2 {
    grid-column: span 2;
}