/* Scalify Design System - Variables */

:root {
  /* ==========================================
     COLOR PALETTE - Notion-inspired
     ========================================== */
  
  /* Background Colors */
  --bg-primary: #ffffff;
  --bg-secondary: #f7f7f5;
  --bg-tertiary: #f1f1ef;
  --bg-hover: #efefef;
  --bg-active: #e8e8e6;
  
  /* Text Colors */
  --text-primary: #37352f;
  --text-secondary: #6b6b6b;
  --text-muted: #9b9a97;
  --text-placeholder: #c4c4c4;
  --text-inverse: #ffffff;
  
  /* Accent Colors - Scalify Orange */
  --accent-primary: #E9A23B;
  --accent-primary-hover: #d4922f;
  --accent-primary-light: #fdf5e9;
  
  /* Semantic Colors */
  --color-success: #0f7b6c;
  --color-success-light: #e6f4f1;
  --color-warning: #d9730d;
  --color-warning-light: #fdf3e7;
  --color-error: #e03e3e;
  --color-error-light: #fce8e8;
  --color-info: #2383e2;
  --color-info-light: #e8f2fc;
  
  /* Border Colors */
  --border-light: #e9e9e7;
  --border-default: #d3d3d0;
  --border-dark: #c4c4c4;
  --border-focus: var(--accent-primary);
  
  /* Shadow */
  --shadow-xs: 0 1px 2px rgba(0, 0, 0, 0.04);
  --shadow-sm: 0 1px 3px rgba(0, 0, 0, 0.06), 0 1px 2px rgba(0, 0, 0, 0.04);
  --shadow-md: 0 4px 6px rgba(0, 0, 0, 0.06), 0 2px 4px rgba(0, 0, 0, 0.04);
  --shadow-lg: 0 10px 15px rgba(0, 0, 0, 0.08), 0 4px 6px rgba(0, 0, 0, 0.04);
  --shadow-xl: 0 20px 25px rgba(0, 0, 0, 0.1), 0 10px 10px rgba(0, 0, 0, 0.04);
  
  /* ==========================================
     TYPOGRAPHY
     ========================================== */
  
  --font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  --font-mono: 'SF Mono', 'Fira Code', 'Consolas', monospace;
  
  /* Font Sizes */
  --text-xs: 11px;
  --text-sm: 12px;
  --text-base: 14px;
  --text-lg: 16px;
  --text-xl: 18px;
  --text-2xl: 24px;
  --text-3xl: 30px;
  --text-4xl: 36px;
  
  /* Font Weights */
  --font-normal: 400;
  --font-medium: 500;
  --font-semibold: 600;
  --font-bold: 700;
  
  /* Line Heights */
  --leading-tight: 1.25;
  --leading-normal: 1.5;
  --leading-relaxed: 1.75;
  
  /* ==========================================
     SPACING
     ========================================== */
  
  --space-0: 0;
  --space-1: 4px;
  --space-2: 8px;
  --space-3: 12px;
  --space-4: 16px;
  --space-5: 20px;
  --space-6: 24px;
  --space-8: 32px;
  --space-10: 40px;
  --space-12: 48px;
  --space-16: 64px;
  
  /* ==========================================
     BORDER RADIUS
     ========================================== */
  
  --radius-sm: 3px;
  --radius-md: 6px;
  --radius-lg: 8px;
  --radius-xl: 12px;
  --radius-full: 9999px;
  
  /* ==========================================
     TRANSITIONS
     ========================================== */
  
  --transition-fast: 100ms ease;
  --transition-normal: 200ms ease;
  --transition-slow: 300ms ease;
  
  /* ==========================================
     LAYOUT
     ========================================== */
  
  --sidebar-width: 260px;
  --sidebar-collapsed-width: 60px;
  --header-height: 52px;
  --content-max-width: 1200px;
  
  /* ==========================================
     Z-INDEX
     ========================================== */
  
  --z-dropdown: 100;
  --z-sticky: 200;
  --z-modal-backdrop: 300;
  --z-modal: 400;
  --z-tooltip: 500;
  --z-notification: 600;
}

/* Dark Theme */
[data-theme="dark"] {
  --bg-primary: #191919;
  --bg-secondary: #202020;
  --bg-tertiary: #2a2a2a;
  --bg-hover: #333333;
  --bg-active: #3d3d3d;
  
  --text-primary: #e6e6e6;
  --text-secondary: #a3a3a3;
  --text-muted: #6b6b6b;
  --text-placeholder: #4a4a4a;
  
  --border-light: #2d2d2d;
  --border-default: #3d3d3d;
  --border-dark: #4d4d4d;
  
  --accent-primary-light: #3d3325;
  --color-success-light: #1a3330;
  --color-warning-light: #3d2d1a;
  --color-error-light: #3d2020;
  --color-info-light: #1a2d3d;
}
